.post-card {
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  margin: 0 auto 16px;
  max-width: 800px;
  padding: 16px;
}
.post-card .title {
  font-size: 24px;
  font-weight: bold;
}
.post-card .info {
  font-size: 14px;
  margin: 10px 0;
}
.post-card .info i {
  margin-right: 4px;
}
.post-card .info em {
  color: #7FC9FE;
}
.post-card .info .create-at em {
  font-weight: 600;
}
.post-card .summary {
  margin: 10px 0;
  word-break: break-all;
}



