.edit-panel {
  height: 100%;
  overflow: hidden;

  .write, .preview {
    float: left;
    height: 100%;
    width: 50%;
    box-sizing: border-box;
    padding: 20px;
  }

  .title {
    margin-bottom: 10px;
    font-size: 32px;
    padding-bottom: 10px;
  }

  .write {
    position: relative;

    .title {
      display: block;
      width: 100%;
      border-top: none;
      border-right: none;
      border-left: none;
      font-size: 32px;

      &:focus {
        outline: none;
      }
    }

    textarea {
      width: 100%;
      height: e("calc(100% - 80px)");
      resize: none;
      padding: 0;
      border: none;
      font-size: 16px;
      line-height: 1.5;

      &:focus {
        outline: none;
      }
    }

    .action {
      width: 100%;

      a {
        float: right;
      }
    }
  }

  .preview {
    border-left: 1px solid black;

    .content {
      height: e("calc(100% - 70px)");

      ul, ol {
        padding-left: 20px;
      }
    }
  }
}



