.page-admin {
  padding-left: 100px;
  height: 100%;
}
.page-admin .siderbar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: blue;
  width: 100px;
}
.page-admin .siderbar a {
  color: white;
  text-decoration: none;
  display: block;
  text-align: center;
  padding: 10px 0;
}
.page-admin .siderbar a:hover {
  background-color: white;
  color: black;
}
.page-admin .siderbar a.router-link-active {
  background-color: white;
  color: black;
}



