































.post-card {
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  margin: 0 auto 16px;
  max-width: 800px;
  padding: 16px;

  .title {
    font-size: 24px;
    font-weight: bold;
  }

  .info {
    font-size: 14px;
    margin: 10px 0;

    i {
      margin-right: 4px;
    }

    em {
      color: #7FC9FE;
    }

    .create-at {
      em {
        font-weight: 600;
      }
    }
  }

  .summary {
    margin: 10px 0;
    word-break: break-all;
  }
}



