
























.page-admin {
  padding-left: 100px;
  height: 100%;

  .siderbar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    background-color: blue;
    width: 100px;

    a {
      color: white;
      text-decoration: none;
      display: block;
      text-align: center;
      padding: 10px 0;

      &:hover {
        background-color: white;
        color: black;
      }

      &.router-link-active {
        background-color: white;
        color: black;
      }
    }
  }
}



