.friend-card {
  border: 1px solid #efd7ef;
  border-radius: 5px;
  box-sizing: border-box;
  width: calc(25% - 16px * 2);
  margin: 16px;
  float: left;
  text-align: center;
}
.friend-card .basic-info {
  padding: 16px;
}
.friend-card .avatar-container {
  width: 80px;
  height: 80px;
  border-radius: 80px;
  border: 1px solid rgba(0, 0, 0, 0.08);
  overflow: hidden;
  margin: auto;
}
.friend-card .avatar-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.friend-card .name {
  margin: 16px 0;
}
.friend-card .intro {
  color: gray;
  font-size: 14px;
  height: 38px;
}
.friend-card a {
  height: 35px;
  line-height: 35px;
  font-size: 14px;
  border-top: 1px solid #efd7ef;
  display: block;
}
.friend-card a:hover {
  background-color: #e6e6e6;
}
@media screen and (max-width: 1020px) {
  .friend-card {
    width: calc(33.33% - 16px * 2);
    margin: 16px;
  }
}
@media screen and (max-width: 660px) {
  .friend-card {
    width: calc(50% - 16px * 2);
    margin: 16px;
  }
}



