@import '../../node_modules/highlight.js/styles/github.css';
.edit-panel {
  height: 100%;
  overflow: hidden;
}
.edit-panel .write,
.edit-panel .preview {
  float: left;
  height: 100%;
  width: 50%;
  box-sizing: border-box;
  padding: 20px;
}
.edit-panel .title {
  margin-bottom: 10px;
  font-size: 32px;
  padding-bottom: 10px;
}
.edit-panel .write {
  position: relative;
}
.edit-panel .write .title {
  display: block;
  width: 100%;
  border-top: none;
  border-right: none;
  border-left: none;
  font-size: 32px;
}
.edit-panel .write .title:focus {
  outline: none;
}
.edit-panel .write textarea {
  width: 100%;
  height: calc(100% - 80px);
  resize: none;
  padding: 0;
  border: none;
  font-size: 16px;
  line-height: 1.5;
}
.edit-panel .write textarea:focus {
  outline: none;
}
.edit-panel .write .action {
  width: 100%;
}
.edit-panel .write .action a {
  float: right;
}
.edit-panel .preview {
  border-left: 1px solid black;
}
.edit-panel .preview .content {
  height: calc(100% - 70px);
}
.edit-panel .preview .content ul,
.edit-panel .preview .content ol {
  padding-left: 20px;
}



